nav {
  position: relative;
  height: 22rem;
  color: $color-white;

  .menu {
    position: absolute;
    top: $size--padding__header;
    right: $size--padding__header;
    left: $size--padding__header;
    max-width: $size--max-width__main;
    list-style: none;
    margin: 0 auto;
    padding: 0;
    display: flex;
    justify-content: space-between;

  }
  @include media("<tablet") {
      height: 14rem;
  }
}
